@import 'https://unpkg.com/sanitize.css/evergreen.css';
@import 'https://unpkg.com/sanitize.css/forms.evergreen.css';

@font-face {
	font-family: 'Brandon Grotesque';
	src: url('./fonts/BrandonGrotesque-Bold.woff2') format('woff2'),
		url('./fonts/BrandonGrotesque-Bold.woff') format('woff'),
		url('./fonts/BrandonGrotesque-Bold.ttf') format('truetype');
	font-weight: bold;
	font-style: normal;
}

@font-face {
	font-family: 'Brandon Grotesque';
	src: url('./fonts/BrandonGrotesque-Regular.woff2') format('woff2'),
		url('./fonts/BrandonGrotesque-Regular.woff') format('woff'),
		url('./fonts/BrandonGrotesque-Regular.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'Brandon Grotesque';
	src: url('./fonts/BrandonGrotesque-RegularItalic.woff2') format('woff2'),
		url('./fonts/BrandonGrotesque-RegularItalic.woff') format('woff'),
		url('./fonts/BrandonGrotesque-RegularItalic.ttf') format('truetype');
	font-weight: normal;
	font-style: italic;
}

@font-face {
	font-family: 'Brandon Grotesque';
	src: url('./fonts/BrandonGrotesque-Black.woff2') format('woff2'),
		url('./fonts/BrandonGrotesque-Black.woff') format('woff'),
		url('./fonts/BrandonGrotesque-Black.ttf') format('truetype');
	font-weight: 900;
	font-style: normal;
}

@font-face {
	font-family: 'Brandon Grotesque';
	src: url('./fonts/BrandonGrotesque-Medium.woff2') format('woff2'),
		url('./fonts/BrandonGrotesque-Medium.woff') format('woff'),
		url('./fonts/BrandonGrotesque-Medium.ttf') format('truetype');
	font-weight: 500;
	font-style: normal;
}

:root {
	--color-brand: #215184;
}

body {
	margin: 0;
	font-family: 'Brandon Grotesque', system-ui, Arial, Helvetica, sans-serif;
	color: #545454;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
